import store from '@/store/index.js'
import http from './http.js'

let timer = -1

function info () {
  return store.state.authInfo
}

export function save (data) {
  let authInfo = info()
  if (authInfo && data && data.type < authInfo.type) {
    return
  }
  store.commit('authInfo', data)
  retry(-1)
}

function retry (time = 0, cb) {
  clearTimeout(timer)
  let authInfo = info()
  if (!authInfo || authInfo.type === 0) {
    if (!authInfo) {
      time = 0
    }

    // 过期时间大于1小时则过期前一小时重新获取
    // 否则2/3时获取
    if (time === -1) {
      time = authInfo.tokenExpire - Date.now()
      if (time < 3600000) {
        time = Math.floor(time / 3 * 2)
      } else {
        time -= 3600000
      }
    }

    timer = setTimeout(() => authNomral(cb), time)
  }
}

let fetching = false
export function authNomral (cb) {
  let authInfo = info()
  if (fetching || authInfo && authInfo.type > 0) {
    return
  }

  fetching = true
  http.post('/api/common/user/token', {
    deviceId: 'app-device'
  }, { auth: false }).then(data => {
    save({
      type: 0,
      token: data.token,
      tokenExpire: data.tokenExpire
    })
    cb && cb()
  }, () => {
    retry(2000, cb)
  }).finally(() => fetching = false)
}

export function isExpired (time = 600000) {
  let authInfo = info()
  if (!authInfo) {
    console.log('Unauthorized')
    return true
  }

  let expire = authInfo.tokenExpire
  if (expire <= Date.now()) {
    console.log('Token is expired')
    save(null)
    return true
  }

  // 提前10分钟获取
  if (expire <= Date.now() + time) {
    console.log('Token will expire', time)
    retry()
  }

  return false
}
