import { Dialog } from 'vant'
import i18n from '@/i18n/index.js'
import store from '@/store/index.js'
import router from '@/router/index.js'
import http from './http.js'
import EventBus from './event-bus.js'
import JsNativeApi from './js-native-api.js'

export default {
  back (depth = -1) {
    EventBus.$emit('back', depth)
  },
  tabbar (tab) {
    // EventBus.$emit('tabbar')
    replace(tab)
  },
  escheduler (redirect = false) {
    if (redirect) {
      replace('escheduler')
    } else {
      router.push({
        name: 'escheduler'
      })
    }
  },
  entity (id) {
    if (id) {
      router.push({
        name: 'entity',
        params: {
          id
        }
      })
    }
  },
  web (url) {
    if (url) {
      router.push({
        name: 'web',
        query: {
          url: encodeURIComponent(url)
        }
      })
    }
  },
  ticket (id) {
    if (id) {
      router.push({
        name: 'ticket',
        params: {
          id
        }
      })
    }
  },
  scan,
  alert,
  confirm: vconfirm,
  sign,
  saveRoute,
  popRoute,
  to,
  verify,
  getStatus,
  toBuyTicket (type = 0) {
    to(`/pages/ticket/buy/buy?type=${ type }`)
  },
  linkTo,
  readMail,
  phone (phoneNumber) {
    JsNativeApi.ringUp(phoneNumber)
  }
}

function replace (name) {
  router.replace({
    name
  })
}

