function alert (options = {}) {
  if (typeof options === 'string') {
    options = { message: options }
  }
  let { title = i18n.t('modal.title'), message, btn = i18n.t('btn.ok') } = options
  return Dialog.alert({
    className: 'c-dialog',
    title: title === false ? false : title,
    message,
    confirmButtonText: btn
  })
}

function vconfirm (options = {}) {
  if (typeof options === 'string') {
    options = { message: options }
  }
  let { title = false, message, btn = i18n.t('btn.confirm'), cbtn = i18n.t('btn.cancel') } = options
  return Dialog.confirm({
    className: 'c-dialog',
    title: title === false ? false : title,
    message,
    confirmButtonText: btn,
    cancelButtonText: cbtn
  })
}

function sign () {
  router.push({
    name: 'sign'
  })
}

let $CacheedRoute = null

function saveRoute (route) {
  if (route) {
    $CacheedRoute = route
  } else {
    $CacheedRoute = {
      name: router.currentRoute.name,
      params: {
        ...router.currentRoute.params
      },
      query: {
        ...router.currentRoute.query
      }
    }
  }
}

function popRoute () {
  if ($CacheedRoute) {
    router.replace($CacheedRoute)
    $CacheedRoute = null
  }
}

