<template>
  <div class="l-page">
    <v-nav :title="$t('common.mail.title')"/>
    <div class="l-flex--row has-shadow--short u-background--blue">
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 0 }" @click="tabIndex = 0">{{ $t('common.mail.account') }}</div>
      <div class="o-tab l-flex__fill" :class="{ 'is-selected': tabIndex === 1 }" @click="tabIndex = 1">{{ $t('common.mail.park') }}</div>
    </div>
    <div class="l-hscroll l-flex__fill">
      <div class="l-hscroll__wrapper" :class="{ 'is-second': tabIndex === 1 }">
        <div class="l-hscroll__item u-scroll-y">
          <div
            v-for="(mail, index) in userMails"
            :key="index"
            class="c-mail has-padding--around has-hairline--b"
            :class="{ 'is-fresh': mail.isFresh }"
            @click="readMail(mail)"
          >
            <div class="c-mail__content">{{ mail.origin.msgContent }}</div>
            <div class="c-mail__time">{{ mail.time }}</div>
          </div>
        </div>
        <div class="l-hscroll__item u-scroll-y">
          <div
            v-for="(mail, index) in parkMails"
            :key="index"
            class="c-mail has-padding--around has-hairline--b"
            :class="{ 'is-fresh': mail.isFresh }"
            @click="readMail(mail)"
          >
            <div class="c-mail__content">{{ mail.origin.msgTitle }}</div>
            <div class="c-mail__time">{{ mail.time }}</div>
          </div>
        </div>
      </div>
    </div>
    <transition :name="transition">
      <router-view style="z-index: 9;"/>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { initMails } from '@/utils/common.js'

export default {
  data () {
    return {
      tabIndex: 0,
      transition: 'slide-r2l'
    }
  },
  computed: {
    ...mapState(['userMails', 'parkMails']),
    isMailRoute () {
      return this.$route.name === 'mail'
    },
    hasUserMails () {
      return this.userMails.length > 0
    },
    hasParkMails () {
      return this.parkMails.length > 0
    }
  },
  methods: {
    readMail (mail) {
      this.$features.readMail(mail)
    }
  },
  created () {
    initMails()
  },
  beforeRouteUpdate (to, from, next) {
    if (to.name === 'mail') {
      this.transition = 'slide-l2r'
    } else {
      this.transition = 'slide-r2l'
    }
    this.$nextTick(next)
  }
}
</script>

<style lang="scss" scoped>
.c-mail {
  &__content {
    font-size: 28px;
    color: $color--dark;
    line-height: 36px;
    @include u-font;
  }

  &__time {
    margin-top: 16px;
    font-size: 24px;
    color: $color;
    line-height: 32px;
  }

  &.is-fresh {
    background-color: rgba($color-blue, .1);

    .c-mail__time {
      color: $color-blue;
    }
  }
}
</style>
