<template>
  <list-page
    :title="$t('settings.account.profile.title')"
  >
    <template #nav>
      <span class="is-content--blue" @click="save">{{ $t('btn.save') }}</span>
    </template>
    <i
      class="o-avatar"
      :style="avatarStyle"
      @click="toEdit"
    >
      <i
        v-if="!loginByEmail"
        class="o-edit"
      ></i>
    </i>
    <field
      class="c-vlist-item--nearer u-separate--far"
      v-model="name"
      :title="$t('settings.account.profile.name')"
      :placeholder="$t('settings.account.profile.name')"
      :error="nameError"
      @input="nameError = ''"
    />
    <field
      v-if="!loginByEmail"
      class="c-vlist-item--nearer"
      v-model="nick"
      :title="$t('settings.account.profile.nick')"
      :placeholder="$t('settings.account.profile.nick')"
      :error="nickError"
      maxlength="12"
      @input="nickError = ''"
    />
    <field
      class="c-vlist-item--nearer"
      :value="birthDay"
      :title="$t('sign.signup.birth')"
      :placeholder="$t('sign.signup.birthPlaceholder')"
      readonly
      clickable
      @click="onClickBirth"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="country2city"
      :title="$t('sign.signup.country')"
      :placeholder="$t('sign.signup.countryPlaceholder')"
      readonly
      clickable
      @click="onClickCountry"
    />
    <van-popup
      v-if="showBirthPicker"
      v-model="showBirth"
      position="bottom"
      @closed="onHideBirth"
    >
      <van-datetime-picker
        type="date"
        :value="currentDate"
        :min-date="minDate"
        :max-date="maxDate"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmBirth"
        @cancel="showBirth = false"
      />
    </van-popup>
    <van-popup
      v-if="showCountryPicker"
      v-model="showCountry"
      position="bottom"
      @closed="onHideCountry"
    >
      <van-picker
        :columns="columns"
        :show-toolbar="true"
        :confirm-button-text="$t('btn.done')"
        :cancel-button-text="$t('btn.cancel')"
        @confirm="onConfirmCountry"
        @cancel="showCountry = false"
        @change="onChangeCountry"
      />
    </van-popup>
    <input ref="fileUpload" type="file" accept="image/*" style="display: none;" @change="handleFile"/>
  </list-page>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { checkName, checkNickName } from '@/utils/patt.js'
import { dateFormat } from '@/utils/format.js'
import { Popup, DatetimePicker, Picker } from 'vant'
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    [ Popup.name ]: Popup,
    [ DatetimePicker.name ]: DatetimePicker,
    [ Picker.name ]: Picker,
    ListPage,
    Field
  },
  data () {
    let today = new Date()

    return {
      os: '',
      avatar: null,
      name: '',
      nameError: '',
      nick: '',
      nickError: '',
      birth: '',
      minDate: new Date(today.getFullYear() - 100, 0, 1),
      maxDate: today,
      showBirth: false,
      showBirthPicker: false,
      country: '',
      columns: null,
      showCountry: false,
      showCountryPicker: false
    }
  },
  computed: {
    ...mapState(['countryList']),
    ...mapGetters(['baseUrl', 'loginByEmail', 'avatarUrl']),
    isAndroid () {
      return this.os === 'android'
    },
    isIos () {
      return this.os === 'ios'
    },
    avatarStyle () {
      console.log(this.avatar)
      if (this.avatar) {
        let avatar
        if (this.avatar.cosUrl) {
          avatar = this.avatar.cosUrl
        } else if (this.baseUrl && this.avatar.url) {
          avatar = `${ this.baseUrl }${ this.avatar.url }`
        }
        return avatar ? { backgroundImage: `url("${ avatar }")` } : null
      }

      return this.avatarUrl ? { backgroundImage: `url("${ this.avatarUrl }")` } : null
    },
    birthDay () {
      if (this.birth) {
        return dateFormat(this.birth, 'MM-dd-yyyy')
      }
      return ''
    },
    currentDate () {
      return this.birth || new Date()
    },
    country2city () {
      if (this.country && this.countryList.length > 0) {
        let country = this.countryList[this.country[0]]
        if (country && country.children) {
          let province = country.children[this.country[1]]
          return `${ country.name } ${ province ? province.name : '' }`
        }
      }
      return ''
    }
  },
  methods: {
    upload (file, type) {
      this.$http.upload('/api/common/uploadPic', { file, type }, {
        loading: true,
        server: true
      }).then(data => this.avatar = data)
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      this.$refs.fileUpload.value = null
      this.upload(file, 'file')
    },
    toEdit () {
      if (!this.loginByEmail) {
        if (this.isAndroid) {
          if (this.$refs.fileUpload) {
            this.$refs.fileUpload.click()
          }
        } else if (this.isIos) {
          this.$native.pickimage().then(result => {
            let file = result && result.image
            if (file) {
              let bytes = window.atob(file)
              let array = []
              for (let i = 0; i < bytes.length; i++) {
                array.push(bytes.charCodeAt(i))
              }
              this.upload(new Blob([new Uint8Array(array)], { type: 'image/jpeg' }), 'blob')
            }
          })
        }
      }
    },
    onClickBirth () {
      this.showBirthPicker = true
      this.$nextTick(() => this.showBirth = true)
    },
    onHideBirth () {
      this.showBirthPicker = false
    },
    onConfirmBirth (date) {
      this.birth = date
      this.showBirth = false
    },
    getProvinceList (index, picker) {
      if (!this.countryList[index].children) {
        this.$http.post('/api/common/area/list', {
          country: this.countryList[index].id
        }).then(data => {
          this.countryList[index].children = data.provinceList
          if (this.$country && index === this.$country[0]) {
            if (picker) {
              if (picker.getColumnValues(1).length === 0) {
                picker.setColumnValues(1, this.countryList[index].children.map(province => province.name))
              }
            } else {
              if (this.columns[1].values.length === 0) {
                this.columns[1].values = data.provinceList.map(province => province.name)
              }
            }
          }
        })
      }
    },
    onClickCountry () {
      if (this.countryList.length > 0) {
        this.$country = this.country ? [...this.country] : [0, 0]
        this.columns = [
          {
            values: this.countryList.map(country => country.name),
            defaultIndex: this.$country[0]
          },
          {
            values: (this.countryList[this.$country[0]].children || []).map(province => province.name),
            defaultIndex: this.$country[1]
          }
        ]
        this.getProvinceList(this.$country[0])
        this.showCountryPicker = true
        this.$nextTick(() => this.showCountry = true)
      }
    },
    onHideCountry () {
      this.showCountryPicker = false
    },
    onChangeCountry (picker, values, index) {
      this.$country[index] = picker.getColumnIndex(index)
      if (index === 0) {
        picker.setColumnValues(1, (this.countryList[this.$country[index]].children || []).map(province => province.name))
        this.getProvinceList(this.$country[index], picker)
      }
    },
    onConfirmCountry (values, indexs) {
      this.$country = null
      if (values[1] !== void 0) {
        this.country = indexs
        this.countryError = ''
      }
      this.showCountry = false
    },
    save () {
      let nResult = checkName(this.name)
      this.nameError = nResult.msg

      if (!this.loginByEmail) {
        let nickResult = checkNickName(this.nick)
        this.nickError = nickResult.msg

        if (!nickResult.pass) {
          return
        }
      }

      if (nResult.pass) {
        this.$toast('profile')
        // this.$features.back()
      }
    }
  },
  created () {
    if (!this.loginByEmail) {
      this.$native.getNativeOS().then(os => {
        this.os = os
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.o-avatar {
  display: inline-block;
  position: relative;
  width: 216px;
  height: 216px;
  border-radius: 50%;
  background: url(~@/assets/avatar.png) 0 0 / 100% 100% no-repeat;
}

.o-edit {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  overflow: hidden;
  background-color: $color-blue;

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 32px;
    height: 32px;
    transform: translate(-50%, -50%);
    background: url(~@/assets/icon-edit.png) 0 0 / 100% 100% no-repeat;
  }
}
</style>
